import React, { useEffect, useState } from "react";
import sanityClient from "../client";
import bgImage from "../logo/glamorous.svg";

export default function Project() {
    const [projectData, setProjectData] = useState(null);

    useEffect(() => {
        sanityClient.fetch(`*[_type == "project"]{
            title,
            date,
            place,
            mainImage,
            description,
            projectType,
            link,
            tags
        }`).then((data) => setProjectData(data)).catch(console.error);
    }, []);

    return (<main>
        <img src={bgImage} alt="circuit board" className="absolute object-cover w-full h-full" style={{ backgroundColor: "#708090", opacity: 0.1, }} />
        <section>
            <h1 className="text-4xl font-bold text-blue-900 font-serif text-center" style={{ textShadow: "#000 2px 2px 3px" }}>My Projects</h1>
            <section className="grid grid-cols-2 gap-8">
                {projectData && projectData.map((project, index) => (
                    <article className="relative rounded-lg shadow-xl bg-white p-16">
                        <h3 className="text-gray-800 text-3xl font-bold mb-2 hover:text-green-700"><a href={project.link} alt={project.title} target="_blank"  >{project.title}</a></h3>
                        <div className="text-gray-500 text-xs space-x-4">
                            <span><strong className="font-bold">Finished on :</strong>{" "}{new Date(project.date).toLocaleDateString()}</span>
                            <span><strong className="font-bold">Company</strong>{" "}{project.place}</span>
                            <span><strong className="font-bold">Type</strong>{" "}{project.projectType}</span>
                            <p className="my-6 text-lg text-gray-700 leading-relaxed">{project.description}</p>
                            <a href={project.link} alt={project.title} target="_blank" className="text-blue-700 font-bold hover:underline hover:text-green-700">View Project {" "}<span role="img" aria-label="right pointer">âž¡</span></a>

                        </div>
                    </article>
                ))}
            </section>
        </section>
    </main>)
};